.post {
  width: 100%;
  max-width: 1280px;
  text-align: left;

  &-date {
    &:after {
      content: '—';
    }
  }

  &-title {
    font-size: 2.625rem;
    margin: 0 0 20px;
    @media #{$media-size-phone} {
      font-size: 2rem;
    }

    a {
      text-decoration: none;
    }
  }

  &-tags {
    display: block;
    margin-bottom: 20px;
    font-size: 1rem;
    opacity: 0.5;

    a {
      text-decoration: none;
    }
  }

  // &-content {
  //   display: flex;
  //   flex-direction: column;
  //   align-items: center;
  //   max-width: 1280px;
  // }

  &-cover {
    border-radius: 8px;
    margin: 40px -50px;
    width: $max-width;
    max-width: $max-width;
    overflow: hidden;
    @media #{$media-size-tablet} {
      margin: 20px 0;
      width: 100%;
    }
  }

  &-excerpt {
    color: grey;
    font-style: italic;
  }

  &-info {
    margin-top: 30px;
    font-size: 0.8rem;
    line-height: normal;
    @include dimmed;

    p {
      margin: 0.8em 0;
    }

    a:hover {
      border-bottom: 1px solid white;
    }

    svg {
      margin-right: 0.8em;
    }

    .tag {
      margin-right: 0.5em;

      &::before {
        content: "#";
      }
    }

    .feather {
      display: inline-block;
      vertical-align: -.125em;
      width: 1em;
      height: 1em;
    }
  }

  &-audio {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px;

    audio {
      width: 90%;
    }
  }

  .flag {
    border-radius: 50%;
    margin: 0 5px;
  }
}

.pagination {
  margin-top: 20px;

  &__title {
    display: flex;
    text-align: center;
    position: relative;
    margin: 20px 0;

    &-h {
      text-align: center;
      margin: 0 auto;
      padding: 5px 10px;
      font-size: 0.8rem;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 0.1em;
      z-index: 1;
      // Default
      background: $light-background;
      color: $light-color-secondary;

      @media (prefers-color-scheme: dark) {
        background: $dark-background;
        color: $dark-color-secondary;
      }
    
      @media (prefers-color-scheme: light) {
        background: $light-background;
        color: $light-color-secondary;
      }
    
      [data-theme=dark] & {
        background: $dark-background;
        color: $dark-color-secondary;
      }
    
      [data-theme=light] & {
        background: $light-background;
        color: $light-color-secondary;
      }
    }

    hr {
      position: absolute;
      left: 0;
      right: 0;
      width: 100%;
      margin-top: 15px;
      z-index: 0;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: center;

    a {
      text-decoration: none;
      font-weight: bold;
    }
  }
}

.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  max-width: 40%;
  padding: 0;
  cursor: pointer;
  appearance: none;
  // Default
  background: $light-background-secondary;

  @media (prefers-color-scheme: dark) {
    background: $dark-background-secondary;
  }

  @media (prefers-color-scheme: light) {
    background: $light-background-secondary;
  }

  [data-theme=dark] & {
    background: $dark-background-secondary;
  }

  [data-theme=light] & {
    background: $light-background-secondary;
  }

  + .button {
    margin-left: 10px;
  }

  a {
    display: flex;
    padding: 8px 16px;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &__text {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &.next .button__icon {
    margin-left: 8px;
  }

  &.previous .button__icon {
    margin-right: 8px;
  }
}

.about {
  width: 100%;
  display: flex;
  align-items: center;

  @media #{$media-size-phone} {
    flex-direction: column;
  }

  &-text {
    text-align: left;
    margin-top: -5%;
  }

  &-portrait {
    width: 25%;
    flex-shrink: 0;
    margin-right: 5%;

    @media #{$media-size-phone} {
      width: 50%;
    }
  }

  img {
    width: 100%;
    border-radius: 50%;
  }
  
  &-icons {
    color: $accent-color;
    margin-top: 5%;
    text-align: center;

    @media #{$media-size-phone} {
      display: none;
    }
  }
}

.portfolio {
  width: 100%;
  text-align: left;
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: "Roboto", sans-serif;
    
    [data-theme=dark] & {
      border-bottom: $dark-color solid 1px;
    }
    [data-theme=light] & {
      border-bottom: $light-color solid 1px;
    }
  }

  &-content {
    display: grid;
    row-gap: 16px;
    column-gap: 16px;
    grid-template-columns: repeat(4, 1fr);

    @media #{$media-size-phone} {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &-item {
    width: 100%;
    background: $accent-color;
    position: relative;
    overflow: hidden;
    // border: red 2px solid;

    img {
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
    }

    div span {
      position: absolute;
      bottom: 0px;
      left: 0;
      display: block;
      width: 100%;
      font-size: 18px;
      color: $dark-color;
      background-color: $dark-background-header;
      padding: 16px 32px;
      text-align: center;
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
      opacity: 0;
      mask-position: bottom;
      cursor: pointer;
    }
    
    &:hover img {
      opacity: .5;
      -webkit-transform: scale(1.05);
	    transform: scale(1.05);
      cursor: pointer;
    }

    &:hover span {
      bottom: 20px;
	    opacity: 1;
    }
  }
}

.project {
  width: 100%;

  &-content {
    display: grid;
    row-gap: 16px;
    column-gap: 16px;
    grid-template-columns: repeat(2, 1fr);
    [data-theme=dark] & {
      border-top: $dark-color solid 1px;
    }
    [data-theme=light] & {
      border-top: $light-color solid 1px;
    }
  }

  &-grid {
    text-align: left;
  }
}

.resume {
  @media (prefers-color-scheme: dark) {
    color: $dark-background;
  }

  @media (prefers-color-scheme: light) {
    color: $light-background;
  }

  [data-theme=dark] & {
    color: $dark-background;
  }

  [data-theme=light] & {
    color: $light-background;
  }
}