.logo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-decoration: none;
  font-display: auto;

  &__text {
    font-size: 3.125rem;
    line-height: 90%;
    font-weight: thin;
    font-family: 'Roboto', sans-serif;
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
  }
  
  &__subtext {
    font-size: 1.065rem;
    line-height: 90%;
    font-weight: bold;
    font-family: "Roboto", sans-serif;
    color: $accent-color;
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
  }

  .shrinkText {
    margin-top: 5.5%;
    font-size: 1.575rem;
  }

  .shrinkSubtext {
    opacity: 0;
  }

  @media #{$media-size-phone} {
    &__text {
      font-size: 2.125rem;
      line-height: 90%;
      font-weight: thin;
      font-family: 'Roboto', sans-serif;
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
    }
    
    &__subtext {
      font-size: 0.695rem;
      line-height: 90%;
      font-weight: bold;
      font-family: "Roboto", sans-serif;
      color: $accent-color;
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
    }
  
    .shrinkText {
      margin-top: 5.5%;
      font-size: 1.575rem;
    }
  
    .shrinkSubtext {
      opacity: 0;
    }
  }
}